import gql from "graphql-tag";
import { Component } from 'react'
import { withApollo } from 'react-apollo';
import {
  List,
  ListItem,
  ListItemContainer,
  Num,
  A
} from '../ApolloPostList/styles';

const GET_POSTS = gql`
  query allPosts($first: Int!, $skip: Int!) {
    allPosts(orderBy: createdAt_DESC, first: $first, skip: $skip) {
      id
      title
      votes
      url
      createdAt
    }
    _allPostsMeta {
      count
    }
  }
`
const skip = 0;
const variables = {
    first: 10,
    skip: 0
}


class App extends Component {

  constructor(props) {
    super(props);
    
    debugger;
    this.clientQuery = this
      .clientQuery
      .bind(this);
  }

  async clientQuery(client) {
    var {data} = await client.query({ query: GET_POSTS, skip, variables});
    debugger;
    console.log(data)
    return data;
  }
  
  render() {
    return (
      <button onClick={this.clientQuery}>
        Test!
      </button>
    )
  }
}

export default withApollo(App);